<template>
    <div>
        <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>修改信息</el-breadcrumb-item>
      <el-breadcrumb-item>修改个人信息</el-breadcrumb-item>
    </el-breadcrumb>
   <el-form :data="userlist"  ref="form" :model="Form" label-width="80px" >
  <el-form-item label="用户名">
    <el-input v-model="userlist.username" ></el-input>
  </el-form-item>
   <el-form-item label="手机号" >
    <el-input v-model="userlist.phone"></el-input>
  </el-form-item>
   <el-form-item label="密码" >
    <el-input v-model="userlist.password"></el-input>
  </el-form-item>
   <el-form-item label="邮箱" >
    <el-input v-model="userlist.email"></el-input>
  </el-form-item>
   <el-form-item label="账户余额" >
    <el-input v-model="userlist.money"  disabled></el-input>
  </el-form-item>
    <el-form-item label="启用/冻结" >
    <el-input v-model="userlist.state"  disabled></el-input>
  </el-form-item>
<el-button type="primary" @click="change">保存</el-button>
   </el-form>
    </div>
</template>
<script>
export default {
    data(){
      return{
        userlist: {},
        Form:{
        username: '',
        password: '',
        email:'',
        money:'',
        phone: '',
        state: '',
      }
      }
    },
    created(){
      this.getpersonalmessage()
    },
    methods:{
      async getpersonalmessage() {
        var id =  window.sessionStorage.getItem('id')
        const {data:res} = await this.$http.get("getpersonalmessage",{params:{id}});
        if (res.code != 200) return this.$message.error(`获取数据失败`);
        this.userlist = res.data[0][0];
      },
      async change(){
          console.log(this.userlist); 
          const {data:res}  = await this.$http.put("changemessage",this.userlist)
          if (res.code != 200) return this.$message.error(`修改数据失败`);
          this.$message.success(`修改数据成功`)
      }
    } 
}

</script>
 

<style lang="less" scoped>
.el-form{ 
    margin-top: 20px;
}
.el-form-item{
    width:500px;
}
.el-input{
    margin-left:10px;
}
.el-button{
    margin-left:225px ;
}
</style>