<template>
   <div>
     <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>查看信息</el-breadcrumb-item>
      <el-breadcrumb-item>查看冻结账户</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 主体 -->
    <el-table :data="userlist" style="width: 100%" border stripe>
      <el-table-column type="index" label="序号"></el-table-column>
      <el-table-column prop="username" label="用户名" width="100px"></el-table-column>
      <el-table-column prop="password" label="密码" width="100px"></el-table-column>
      <el-table-column prop="money" label="余额"></el-table-column>
      <el-table-column prop="email" label="邮箱"></el-table-column>
      <el-table-column prop="phone" label="手机号" ></el-table-column>
       <el-table-column label="操作">
        <template slot-scope="scope">
          <!-- 删除按钮 -->
          <el-button type="danger" icon="el-icon-delete" size="mini"  @click="userdelete(scope.row.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
     </div>
</template>

<script>
export default {
    data(){
       return {
      userlist: [],
    };
    },
    created() {
    this.getfreezingaccount();
  },
  methods:{
      async getfreezingaccount() {
      const { data: res } = await this.$http.get("getfreezingaccount");
      if (res.code != 200) {
          return this.$message.error(`${res.tips}`);
      }this.userlist = res.data;
  },
  
  //根据id删除对应的信息
    async userdelete(id) {
      //弹框询问是否删除
      const confirmResult = await this.$confirm(
        '此操作将永久删除该用户信息, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).catch((err) => err)
      //如果用户确认删除 返回值为字符串 confirm
      //如果用户取消删除 返回值为字符串 cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      const { data: res } = await this.$http.delete('userdelete',{params:{id}})
      console.log(id);
      if (res.code !== 200) {
        return this.$message.error('删除失败！')
      }
      //提示修改成功
      this.$message.success('删除用户信息成功！')
      // 重新获取用户列表
      this.getfreezingaccount()
    },
}
}
</script>

<style lang="less" scoped>
.el-table {
  margin-top: 20px;
}
</style>