<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item >查看信息</el-breadcrumb-item>
      <el-breadcrumb-item>查看个人信息</el-breadcrumb-item>
    </el-breadcrumb>
    <div>
    <span>账号：</span>   
    <el-input v-model="username"></el-input><br>
     <span>密码：</span>
     <el-input v-model="password"></el-input>
     </div>
  </div>
</template>

<script>
export default {
   data(){
     return{
       username:'',
       password:''
     };
   },
   created(){
      this.getadminmessage();
   },
   methods:{
      async getadminmessage() {
      const { data: res } = await this.$http.get("getadminmessage");
      if (res.code != 200) return this.$message.error(`${res.tips}`);
      this.username = res.data[0].username;
      this.password = res.data[0].password;
      // console.log(res.data);
   }
   }
}
</script>

<style lang="less" scoped>
.el-input{
  width: 350px;
  margin-top: 20px;
}
</style>