<template>
    <div>
         <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>账户资金操作</el-breadcrumb-item>
      <el-breadcrumb-item>存款</el-breadcrumb-item>
    </el-breadcrumb>
        <!-- 主体 -->
     <el-form label-width="150px">
  <el-form-item label="存入金额" >
    <el-input  v-model="money"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="save">提交</el-button>
  </el-form-item>
</el-form>
    </div>
</template>

<script>
export default {
    data(){
      return{
        money:''
      }
    },
    created(){

    },
    methods:{
async save() {
  
      var id =  window.sessionStorage.getItem('id')
      let data1 = {}
      data1.id = id
      data1.money = this.money
      // console.log(data1)
      const data = await this.$http.post("save",data1);
    if (data.data.code != 200) return this.$message.error(`存款失败`);
    this.$message.success('存款成功')
    this.money=''
      // console.log(data)

    }
    }
}
</script>

<style lang="less" scoped>
.el-form{
    margin-top: 25px;
    min-width: 450px;
    max-width: 600px;
}
</style>