<template>
    <div>
         <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>账户资金操作</el-breadcrumb-item>
      <el-breadcrumb-item>转账</el-breadcrumb-item>
    </el-breadcrumb>
        <!-- 主体 -->
     <el-form label-width="150px">
 <el-form-item label="转出账户" >
    <el-input  v-model="username2" ></el-input>
  </el-form-item>
  <el-form-item label="转出金额" >
    <el-input  v-model="outmoney" ></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="Transfermoney">提交</el-button>
  </el-form-item>
</el-form>
    </div>
</template>

<script>
export default {
     data() {
    return {
      username2: '',
      outmoney:''
    };
  },
  created() {},
  methods: {
    async Transfermoney() {
      var id = window.sessionStorage.getItem("id");
      let data1 = {};
      data1.id = id;
    
      data1.money = this.outmoney;
      
      const data = await this.$http.get(`checktouser/${this.username2}`, data1);
        // console.log(res.data);
      if (data.data.code != 200) return this.$message.error(`用户不存在`);
       else if(data.data.touserstate !=0){
         return this.$message.error(`该用户已被冻结`)
       }else{
         data1.toid =  data.data.touserid
         console.log(data1);
         const res = await this.$http.post(`transfer`, data1);
        if (res.data.code != 200) return this.$message.error(`余额不足`);
         this.$message.success('转账成功')
         this.username2=''
           this.outmoney=''
         console.log(res);
       }
      // console.log(data);
    },
  },
}
</script>

<style lang="less" scoped>
.el-form{
    margin-top: 25px;
    min-width: 450px;
    max-width: 600px;
}
</style>