<template>
    <div>
        <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>修改信息</el-breadcrumb-item>
      <el-breadcrumb-item>管理新闻</el-breadcrumb-item>
       <el-button class="addnews" type="primary" @click="addDialogVisible = true">添加新闻</el-button>
    </el-breadcrumb>
      <el-table :data="newslist" style="width: 100%" border stripe>
      <el-table-column type="index" label="序号" width="100px"></el-table-column>
      <el-table-column prop="title" label="标题" ></el-table-column>
      <el-table-column prop="content" label="内容" ></el-table-column>
       <el-table-column prop="author" label="作者" ></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <!-- 修改按钮 -->
          <el-button  type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row)"></el-button>
          <!-- 删除按钮 -->
          <el-button type="danger" icon="el-icon-delete" size="mini"  @click="newsdelete(scope.row.id)"></el-button>
        </template>
      </el-table-column>
      </el-table>
      <!-- 修改新闻的对话框 -->
    <el-dialog title="修改新闻" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
      <el-form :model="editNewsForm" :rules="editNewsFormRules" ref="editNewsFormRef" label-width="70px">
        <el-form-item label="标题" prop="title">
          <el-input v-model="editNewsForm.title"></el-input>
        </el-form-item>
        <el-form-item label="内容" prop="content">
          <el-input v-model="editNewsForm.content"></el-input>
        </el-form-item>
        <el-form-item label="作者" prop="author">
          <el-input v-model="editNewsForm.author"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editNewsInfo">确 定</el-button>
      </span>
    </el-dialog>
     <!-- 添加新闻对话框 -->
    <el-dialog title="添加新闻" @closed="addDialogClosed" :visible.sync="addDialogVisible" width="50%">
      <!-- 内容主体区 -->
      <el-form :model="addNewsForm" :rules="addNewsFormRules" ref="addNewsFormRef" label-width="80px">
       <el-form-item label="标题" prop="title">
          <el-input v-model="addNewsForm.title"></el-input>
        </el-form-item>
        <el-form-item label="内容" prop="content">
          <el-input v-model="addNewsForm.content"></el-input>
        </el-form-item>
        <el-form-item label="作者" prop="author">
          <el-input v-model="addNewsForm.author"></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部区域 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisiable = false">取 消</el-button>
        <el-button type="primary" @click="addNews">确 定</el-button>
      </span>
    </el-dialog>

    </div>
</template>
<script>
export default {
    data(){
      return{
         newslist:[],
          //  添加新闻的表单数据
      addNewsForm: {
        title:'',
        content:'',
        author:''
      },
       //控制添加对话框显示和隐藏
      addDialogVisible: false,
      //控制修改对话框显示和隐藏
      editDialogVisible: false,
        //查询到的新闻对象
      editNewsForm: {},
      }
    },
    created(){
        this.getnews()
    },
    methods:{
     async getnews(){
      const { data: res } = await this.$http.get("getnews");
      if (res.code != 200) return this.$message.error(`${res.tips}`);
      this.newslist = res.data;
    },
    //监听添加新闻对话框关闭的事件
    addDialogClosed() {
      this.$refs.addNewsFormRef.resetFields()
    },
   //展示编辑用户的对话框
    async showEditDialog(data) {
      let obj = Object.assign({},data)
      this.editNewsForm = obj
      this.editDialogVisible = true
    },
    //提交编辑的信息
 editNewsInfo() {
    console.log(this.editNewsForm);
      this.$refs.editNewsFormRef.validate(async (valid) => {
        if (!valid) return
        //发起修改数据请求
        const { data: res } = await this.$http.put('editnews',this.editNewsForm)
        if (res.code !== 200) {
          return this.$message.error('更新新闻信息失败！')
        } 
        //关闭对话框
        this.editDialogVisible = false
        // 重新获取新闻列表
        this.getnews()
        //提示修改成功
        this.$message.success('更新新闻信息成功！')
      })
    },
     //点击按钮添加新闻
     async addNews() {
        const { data: res } = await this.$http.post('addnews', this.addNewsForm)
        if (res.code !== 200) {
          return this.$message.error('添加失败！')
        }
        this.$message.success('添加成功！')
        this.addDialogVisible = false
        // 重新获取新闻列表
        this.getnews()
    },
    //根据id删除对应的信息
    async newsdelete(id) {
      //弹框询问是否删除
      const confirmResult = await this.$confirm(
        '此操作将永久删除该新闻, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
        }
      ).catch((err) => err)
      //如果用户确认删除 返回值为字符串 confirm
      //如果用户取消删除 返回值为字符串 cancel
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      const { data: res } = await this.$http.delete('newsdelete',{params:{id}})
      console.log(id);
      if (res.code !== 200) {
        return this.$message.error('删除失败！')
      }
      //提示修改成功
      this.$message.success('删除用户信息成功！')
      // 重新获取用户列表
      this.getnews()
    },
}
}
</script>


<style lang="less" scoped>
.el-table{
    margin-top: 10px;
}
.addnews{
  display: flex;
  justify-content: flex-end;
  margin-top: -14px;
}
</style>