<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>账户资金操作</el-breadcrumb-item>
      <el-breadcrumb-item>取款</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 主体 -->
    <el-form label-width="150px">
      <el-form-item label="取出金额">
        <el-input v-model="drawmoney"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="Drawmoney">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      drawmoney: "",
    };
  },
  created() {},
  methods: {
    async Drawmoney() {
      var id = window.sessionStorage.getItem("id");
      let data1 = {};
      data1.id = id;
      data1.money = this.drawmoney;
 
      const data = await this.$http.post("withdrawal", data1);
        // console.log(res.data);
      if (data.data.code != 200) return this.$message.error(`余额不足`);
        this.$message.success('取款成功')
      this.drawmoney=''
      console.log(data);
    },
  },
};
</script>

<style lang="less" scoped>
.el-form {
  margin-top: 25px;
  min-width: 450px;
  max-width: 600px;
}
</style>