// import Vue from 'vue'
// import VueRouter from 'vue-router'
import Login from '../components/Login.vue'
import User from '../components/User.vue'
import Admin from '../components/Admin.vue'
import Welcome from '../components/Welcome.vue'
// 管理员操作
import Allaccount from '../components/adminaction/Allaccount.vue'
import Using from '../components/adminaction/Using.vue'
import Freezing from '../components/adminaction/Freezing.vue'
import Personal from '../components/adminaction/Personal.vue'
import Managenews from '../components/adminaction/Managenews.vue'
import Changepassword from '../components/adminaction/Changepassword.vue'
// 用户操作
import Deposit from '../components/useraction/Deposit.vue'
import Drawmoney from '../components/useraction/Drawmoney.vue'
import Transferaccount from '../components/useraction/Transferaccount.vue'
import Viewnews from '../components/useraction/Viewnews.vue'
import Viewrecord from '../components/useraction/Viewrecord.vue'
import Editpassword from '../components/useraction/Editpassword.vue'
import Editpersonal from '../components/useraction/Editpersonal.vue'

Vue.use(VueRouter)


const router = new VueRouter({
  routes:[
  {path: '/',redirect:'/login'},
  {path: '/login', component: Login},

  {path:'/admin',
  component:Admin,
  redirect:'/allusers',
  children:
  [{path:'/welcome',component:Welcome},
  {path:'/allusers',component:Allaccount},
  {path:'/using',component:Using},
  {path:'/freezing',component:Freezing},
  {path:'/personal',component:Personal},
  {path:'/news',component:Managenews},
  {path:'/passwords',component:Changepassword}
]
},
  {path: '/user', 
  component: User,
  redirect:'/deposit',
  children:[ 
    {path:'/welcome',component:Welcome},
    {path:'/deposit',component:Deposit},
    {path:'/drawmoney',component:Drawmoney},
    {path:'/transferaccount',component:Transferaccount},
    {path:'/viewnews',component:Viewnews},
    {path:'/viewrecord',component:Viewrecord},
    {path:'/editpassword',component:Editpassword},
    {path:'/editpersonal',component:Editpersonal},
]

}
]
})

export default router
