<template>
    <div>
        <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>查看信息</el-breadcrumb-item>
      <el-breadcrumb-item>查看新闻</el-breadcrumb-item>
    </el-breadcrumb>
      <el-table :data="newslist" style="width: 100%" border stripe>
      <el-table-column type="index" label="序号" width="100px"></el-table-column>
      <el-table-column prop="title" label="标题" ></el-table-column>
      <el-table-column prop="content" label="内容" ></el-table-column>
       <el-table-column prop="author" label="作者" ></el-table-column>
      </el-table>
    </div>
</template>
<script>
export default {
    data(){
      return{
         newslist:[],
      }
    },
    created(){
        this.getnews()
    },
    methods:{
     async getnews(){
      const { data: res } = await this.$http.get("getnews");
      if (res.code != 200) return this.$message.error(`${res.tips}`);
      this.newslist = res.data;
    },
    
}
}
</script>


<style lang="less" scoped>
.el-table{
    margin-top: 10px;
}
.addnews{
  display: flex;
  justify-content: flex-end;
  margin-top: -14px;
}
</style>