<template>
   <div>
     <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>查看信息</el-breadcrumb-item>
      <el-breadcrumb-item>查看交易记录</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 主体 -->
    <el-table  style="width: 100%" border stripe :data="record">
      <el-table-column type="index" label="序号"></el-table-column>
      <el-table-column prop="username" label="相关用户" ></el-table-column>
      <el-table-column prop="money" label="金额" ></el-table-column>
      <el-table-column prop="time" label="时间" ></el-table-column>
      <el-table-column prop="state" label="状态">
        <template slot-scope="scope">
           {{scope.row.state | toMystate}}
        </template>
      </el-table-column>
      <el-table-column prop="balance" label="余额"></el-table-column>
    </el-table>
    <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totallength"
      ></el-pagination>
     </div>
</template>

<script>
export default {
    data(){
       return {
         queryInfo: {
        query: '',
        //当前页数
        pagenum: 1,
        //当前每页显示多少条数据
        pagesize: 5,
      },
         record:[],
    totallength:''
     
    }
    },
    filters:{
       toMystate: function (value ){
            if (!value) return ''
            let state = ''
            if(value==0){
                state = '存款'
            }else if(value==1){
                state = '取款'
            }else if(value==2){
                state = '转账'
            }else{
                state = '收款'
            }
            return state
        }, 
    },
    created() {
       this.Totallength()
       this.Record()
  },
  methods:{
      async Totallength() {
      var id = window.sessionStorage.getItem("id");

      const data = await this.$http.get(`getcountusertransaction/${id}`);
        console.log(data);
        this.totallength = data.data.rs
  },
     async Record() {
      var id = window.sessionStorage.getItem("id");

      const data = await this.$http.get(`getusertransaction/${id}/${this.queryInfo.pagenum}/${this.queryInfo.pagesize}`);
        console.log(data);
        this.record = data.data.rs
  },
  //监听pagesizes 改变的事件
    handleSizeChange(newSize) {
       console.log(newSize);
      this.queryInfo.pagesize = newSize
      this.Record()
    },
    //监听页码值改变的事件
    handleCurrentChange(newPage) {
      //  console.log(newPage);
      this.queryInfo.pagenum = newPage
      this.Record()
  },
}
}
</script>

<style lang="less" scoped>
.el-table {
  margin-top: 20px;
}
</style>